@use "base/variables" as v;
@use "base/mixins" as m;

.dashboard {
  background-color: v.$grisClaro;

  @include m.tablet {
    display: flex;
    height: 100vh;
  }

  .principal {
    flex: 1;
    overflow: scroll;
  }

  .contenido {
    padding: 2rem;

    .nombre-pagina {
      font-size: 2.5rem;
      text-align: center;
    }
  }

  .formulario {
    input[type="submit"] {
      @include m.boton(v.$indigo, v.$blanco);
      padding: 1rem;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 1.4rem;
    }
  }

  .enlace {
    color: v.$indigo;

    &:hover {
      color: darken($color: v.$indigo, $amount: 20%);
    }
  }
}
