@use "base/mixins" as m;
@use "base/variables" as v;

.crear {
  margin-top: 5rem;

  .uptask {
    @include m.headerDegradado(v.$morado);
  }

  input[type="submit"] {
    margin-top: 1rem;
    @include m.boton(v.$morado, v.$blanco);
  }

  .acciones {
    a {
      color: v.$morado;
    }
  }
}
